import * as React from 'react';
import { isEmpty } from 'lodash-es';
import { ImagePlugin } from '@ali/4ever-bamboo';
import { Plugin, Inline } from '@ali/4ever-cangjie';
import Image from './image';
import createRenderEditable from './createRenderEditable';
import { moFactory } from '@ali/4ever-plugin-image';


export default function createImagePlugin(config: {
  image?: ImagePlugin.ImagePluginConfig;
}): Plugin {
  const imageConfig = config.image || {};

  return {
    ...moFactory({}),
    renderNode(props, controller, next) {
      const { node } = props;
      // @ts-ignore
      if (node.type !== 'image') {
        return next();
      }

      if (isEmpty((node as Inline).data)) {
        return (
          <ImagePlugin.InlinePlaceholder>
            {imageConfig?.locale?.placeholder || 'Image'}
          </ImagePlugin.InlinePlaceholder>
        )
      }

      const { attributes } = props;

      return (
        <Image
          {...attributes}
          // @ts-ignore
          node={node}
          controller={controller}
          // @ts-ignore
          imageConfig={imageConfig}
        />
      );
    },
    renderEditable: createRenderEditable(imageConfig),
  };
}
